<template>
    <div>
        <div style="padding-top: 10px;" class="chlist" v-for="(data,a) in chdata" :key="a">
            <img :src="data.img" alt="" style="width: 100%" @click="listjj(data)">
            <p>
                {{data.tit}}
            </p>
            <div class="mt-b">
                <div class="user">
                    <div>
                        <img :src="data.userimg" alt="">
                    </div>
                    <div>
                        {{data.username}}
                    </div>
                </div>
                <div>
                    {{data.kanguonum}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "chihulist",
        props:{
            chdata:Array
        },
        methods:{
            listjj(data){
                this.$axios.get("/json/chihujianjie.json").then(res=>{
                    for (var i=0;i<res.data.length;i++){
                        if(data.id == res.data[i].id){
                            // console.log("123")
                            var a = true
                        }
                    }
                    if(a){
                        this.$router.push({
                            path:'/chihujj',
                            query:{
                                id: data.id
                            }
                        })
                    }else {
                        alert("暂无数据！")
                    }
                })

            }
        }
    }
</script>

<style scoped>
    .chlist{
        width: 100%;
        padding-left: 30px;
        padding-right: 30px;
        background: white;
        margin-bottom: 20px;
    }
    .chlist>p{
        height: 70px;
        line-height: 70px;
        font-size: 25px;
        border-bottom: 1px solid #e6e6e6;
        box-sizing: border-box;
    }
    .chlist>.mt-b{
        height: 80px;
        line-height: 80px;
        display: flex;
        justify-content: space-between;
    }
    .chlist>.mt-b>.user{
        display: flex;
        justify-content: left;
    }
    .chlist>.mt-b>.user>div:first-child{
        margin-top: 10px;
        height: 60px;
        width: 60px;
        border-radius: 50%;
        overflow: hidden;
    }
    .chlist>.mt-b>.user>div:last-child{
        margin-left: 10px;
        font-size: 24px;
    }
    .chlist>.mt-b>div:last-child{
      font-size: 24px;
      color: #929292;
    }
</style>
